<template>
  <div class="mini_account">
    <v-menu offset-y left>
      <template v-slot:activator="{ on, attrs }">
        <!-- START:BUTTON -->
        <v-btn
          router
          :fab="!userExist"
          :small="!userExist"
          text
          active-class="error"
          v-on="on"
          v-bind="attrs"
        >
          <span class="font-weight-light" v-if="userExist">{{
            firstName
          }}</span>
          <v-icon :right="userExist">{{
            userExist ? "mdi-chevron-down" : "mdi-account"
          }}</v-icon>
        </v-btn>
      </template>
      <!-- START:LIST -->
      <v-list>
        <v-list-item-group color="error">
          <v-list-item
            v-for="(item, i) in items.account"
            :key="i"
            router
            :to="item.route"
          >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <!-- START::REGISTER_BUTTONS -->
          <v-divider></v-divider>
          <div
            class="btn_group px-5 py-2 d-flex flex-column align-center justify-center"
            style="gap: 10px"
          >
            <v-btn router to="/login" color="primary" block v-if="!userExist"
              >login</v-btn
            >
            <v-btn
              router
              to="/signup"
              color="#16bf82"
              class="white--text"
              block
              v-if="!userExist"
              >register</v-btn
            >
            <v-btn
              router
              to="/login"
              block
              color="error"
              @click="logout"
              v-if="userExist"
              >logout</v-btn
            >
          </div>
        </v-list-item-group>
      </v-list>
    </v-menu>
  </div>
</template>

<script>
import data from "@/db/data.json";
export default {
  data: () => ({
    items: data,
    menu: true,
    userExist: false,
    firstName: "",
  }),
  mounted() {
    /*START::__GET_FIRST_NAME_FROM_STORAGE */
    if (localStorage.getItem("user-info")) {
      let user = JSON.parse(localStorage.getItem("user-info")).firstName;
      this.firstName = user;
      this.userExist = true;
    } else {
      this.userExist = false;
    }
    /*END::__GET_FIRST_NAME_FROM_STORAGE */
  },
  methods: {
    show(title) {
      return (title == "login" || title == "signup") && this.userExist;
    },
    logout() {
      localStorage.removeItem("user-info");
      this.userExist = false;
    },
  },
  computed: {
    C_show() {
      return (title) => this.show(title);
    },
  },
};
</script>

<style lang="scss" scoped></style>
