import router from "../router/index";
// import vuetify from "../plugins/vuetify";
export const ecommerce = {
  state: {
    navBarHeight: "empy",
    cart: [],
    dark: true,
    snack: {
      active: false,
      text: "added",
      color: "primary",
      position: "top",
    },
  },
  getters: {
    getTotalPrice(state) {
      if (state.cart.length > 0) {
        return state.cart
          .map((el) => {
            return el.product.price * el.quantity;
          })
          .reduce((a, c) => Number(a) + +c);
      }
    },
  },
  mutations: {
    navBarHeight(state, payload) {
      state.navBarHeight = payload;
    },
    addToCart(state, { product, quantity }) {
      let productInCart = state.cart.find((el) => {
        return el.product.id == product.id;
      });
      if (productInCart) {
        productInCart.quantity += Number(quantity);
        state.snack.active = true;
        state.snack.text = "quantity added";
        state.snack.color = "primary";
        return;
      } else {
        state.cart.push({ product: product, quantity: quantity });
      }
      state.snack.active = true;
      state.snack.text = "added to cart";
      state.snack.color = "indigo";
      localStorage.setItem("cart", JSON.stringify(state.cart));
    },
    removeItemFromCart(state, product) {
      state.cart = state.cart.filter((el) => {
        return el.product.id != product.product.id;
      });
      localStorage.setItem("cart", JSON.stringify(state.cart));
    },
    removeAllItemsFromCart(state) {
      state.cart = [];
      localStorage.setItem("cart", JSON.stringify(state.cart));
    },
    redirectTo(state, page) {
      router.push({ name: page });
    },
    getCartFromLocalStorage(state) {
      state.cart = JSON.parse(localStorage.getItem("cart"));
    },
  },
  actions: {
    A_navBarHeight({ commit }, payload) {
      commit("navBarHeight", payload);
    },
    A_addToCart({ commit }, { product, quantity }) {
      commit("addToCart", { product, quantity });
    },
    A_removeItemFromCart({ commit }, product) {
      commit("removeItemFromCart", product);
    },
    A_removeAllItemsFromCart({ commit }) {
      commit("removeAllItemsFromCart");
    },
    A_redirectTo({ commit }, page) {
      commit("redirectTo", page);
    },
    A_getCartFromLocalStorage({ commit }) {
      commit("getCartFromLocalStorage");
    },
    A_checkMode({ commit }) {
      commit("checkMode");
    },
  },
};
