<script>
import data from "@/db/data.json";
import { mapState } from "vuex";
export default {
  name: "HelloWorld",
  data: () => ({
    items: data,
    imagePosition: "right",
    image: require("../../assets/images/image1.png"),
    displayImage: false,
    style: {
      "background-image":
        "url(" + require("../../assets/images/landing-02.jpg") + ")",
    },
  }),
  props: {
    msg: String,
  },
  mounted() {
    /*START::WELCOME__SNACK_BAR */
    if (localStorage.getItem("snack")) {
      let snack = JSON.parse(localStorage.getItem("snack"));
      if (snack.active) {
        this.$store.state.ecommerce.snack.active = true;
        this.$store.state.ecommerce.snack.text = snack.text;
        this.$store.state.ecommerce.snack.color = snack.color;
        localStorage.removeItem("snack");
      }
    }
    /*START::WELCOME__SNACK_BAR */
  },
  computed: {
    ...mapState({
      navBarHeight: (state) => state.ecommerce.navBarHeight,
      snack: (store) => store.ecommerce.snack,
    }),
    sumImageHeight() {
      return window.innerHeight - this.navBarHeight;
    },
    classObject(hover) {
      return hover ? "primary" : "error";
    },
    // checkMode() {

    // }
  },
  created() {
    if (window.matchMedia("(max-width:599px)").matches) {
      this.imagePosition = "left";
      this.displayImage = false;
    } else {
      this.displayImage = true;
      this.imagePosition = "right";
    }
    window.onresize = () => {
      if (window.matchMedia("(max-width:599px)").matches) {
        this.displayImage = false;
        this.imagePosition = "left";
      } else {
        this.displayImage = true;
        this.imagePosition = "right";
      }
    };
  },
  methods: {
    scrollToSection(sect) {
      let section = document.querySelector(`${sect}`);
      window.scrollTo({
        behavior: "smooth",
        top: section.offsetTop,
      });
    },
  },
};
</script>

