<script>
import { mapActions, mapState } from "vuex";
import data from "@/db/data.json";
import MiniCart from "@/components/Cart/MiniCartC";
import MiniAccount from "@/components/Account/MiniAccountC";
export default {
  data: () => ({
    items: data,
    drawer: false,
    input: "next",
    expand: false,
    darkMode: false,
    userExist: false,
  }),
  mounted() {
    let user = localStorage.getItem("user-info");
    if (user) {
      this.userExist = true;
      if (localStorage.getItem("cart")) {
        this.A_getCartFromLocalStorage();
      }
    } else {
      this.userExist = false;
    }
    this.A_navBarHeight(Number.parseInt(this.$refs.nav.styles.height));
    if (localStorage.getItem("mode")) {
      document.body.classList.add("darkmode");
      this.darkMode = true;
      this.$vuetify.theme.dark = true;
      this.$vuetify.theme.light = false;
    } else {
      this.$vuetify.theme.dark = false;
      this.$vuetify.theme.light = true;
      this.darkMode = false;
      document.body.classList.remove("darkmode");
    }
  },
  methods: {
    ...mapActions(["A_navBarHeight", "A_getCartFromLocalStorage"]),
    mode() {
      let body = document.body;
      if (this.darkMode) {
        body.classList.add("darkmode");
        this.$vuetify.theme.dark = true;
        this.$vuetify.theme.light = false;
        localStorage.setItem("mode", "dark");
      } else {
        body.classList.remove("darkmode");
        this.$vuetify.theme.light = true;
        this.$vuetify.theme.dark = false;
        localStorage.removeItem("mode");
      }
    },
  },
  computed: {
    ...mapState({
      navBarHeight: (state) => state.ecommerce.navBarHeight,
      cart: (store) => store.ecommerce.cart,
    }),
    newHeight() {
      return this.A_navBarHeight(Number.parseInt(this.$refs.nav.styles.height));
    },
  },
  components: {
    MiniCart,
    MiniAccount,
  },
};
</script>

