import Vue from "vue";
import VueRouter from "vue-router";
import HomeView from "../views/HomeView.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  },
  {
    path: "/products",
    name: "products",
    component: () => import(/**productsView */ "@/views/ProductsListView"),
  },
  {
    path: "/contact",
    name: "contact",
    component: () => import(/**contactView */ "@/views/ContactView"),
    meta: {
      nav: false,
    },
  },
  {
    path: "/account",
    name: "account",
    component: () => import(/**accountView */ "@/views/AccountView"),
    meta: {
      nav: false,
    },
  },
  {
    path: "/login",
    name: "login",
    component: () => import(/**accountView */ "@/views/LoginView"),
    meta: {
      nav: false,
    },
  },
  {
    path: "/signup",
    name: "signup",
    component: () => import(/**accountView */ "@/views/SignupView"),
    meta: {
      nav: false,
    },
  },
  {
    path: "/cart",
    name: "cart",
    component: () => import(/**cartView */ "@/views/CartView"),
  },
  {
    path: "/product/:id",
    name: "product",
    component: () => import(/**cartView */ "@/views/ProductView"),
    props: true,
    meta: {
      nav: false,
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
