<template>
  <v-menu
    transition="slide-y-transition"
    offset-y
    open-on-hover
    :close-on-content-click="false"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-badge :content="cart.length == 0 ? '0' : cart.length" overlap icon>
        <v-btn
          router
          to="/cart"
          fab
          small
          text
          active-class="error white--text"
          v-on="on"
          v-bind="attrs"
        >
          <v-icon>mdi-cart-outline</v-icon>
        </v-btn>
      </v-badge>
    </template>
    <v-list v-if="cart.length > 0">
      <v-list
        class="overflow-y-auto"
        style="max-height: 200px"
        v-if="cart.length > 0"
      >
        <template v-for="(item, i) in cart">
          <v-divider :key="`${i}-divider`" v-if="i !== 0"></v-divider>
          <v-list-item :key="`${i}-item`">
            <v-list-item-content>
              <v-img
                max-height="50"
                max-width="50"
                contain
                :src="
                  require('../../assets/images/' + item.product.src + '.jpg')
                "
              ></v-img>
              <v-list-item-title>{{ item.product.title }}</v-list-item-title>
              <v-list-item-subtitle
                ><span>{{ item.quantity }} x</span> $<strong>{{
                  item.product.price
                }}</strong></v-list-item-subtitle
              >
            </v-list-item-content>
            <v-list-item-icon>
              <v-btn @click="A_removeItemFromCart(item)" small color="error">
                <v-icon>mdi-delete</v-icon>
              </v-btn>
            </v-list-item-icon>
          </v-list-item>
        </template>
      </v-list>
      <v-divider v-if="cart.length !== 0"></v-divider>
      <v-list-item v-if="cart.length !== 0">
        <v-list-item-content>
          <v-list-item-title>Total: {{ getTotalPrice }}</v-list-item-title>
        </v-list-item-content>
        <v-list-item-action :class="[cart.length > 1 ? 'pr-4' : '']">
          <v-btn @click="A_removeAllItemsFromCart" small color="error darken-1">
            <v-icon>mdi-delete</v-icon>
          </v-btn>
        </v-list-item-action>
      </v-list-item>
      <!-- <v-list-item v-if="cart.length == 0"> Nothing to show </v-list-item> -->
    </v-list>
  </v-menu>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
export default {
  data: () => ({
    activeClass: false,
    menu: true,
  }),
  computed: {
    ...mapState({
      cart: (store) => store.ecommerce.cart,
    }),
    ...mapGetters(["getTotalPrice"]),
  },
  methods: {
    ...mapActions([
      "A_removeItemFromCart",
      "A_removeAllItemsFromCart",
      "A_redirectTo",
    ]),
  },
};
</script>

<style lang="scss" scoped></style>
