<template>
  <v-snackbar v-model="snack.active" :color="snack.color" top>
    <template v-slot:action="{ on, attrs }">
      <v-btn @click="snack.active = false" v-on="on" v-bind="attrs" small text
        ><v-icon>mdi-close</v-icon></v-btn
      >
    </template>
    <span>{{ snack.text }}</span>
  </v-snackbar>
</template>

<script>
import { mapState } from "vuex";
export default {
  data: () => ({}),
  computed: {
    ...mapState({
      snack: (store) => store.ecommerce.snack,
    }),
  },
};
</script>

